import {
  killHeaderNames,
  weaponHeaderNames,
  summaryHeaderNames,
  technicalHeaderNames,
  getGameVersionFromRawSummarySection
} from "./version/version-api"
import { GameVersion } from "./version/version-types"
import {
  CsvRawSectionContent,
  CsvNamedSections,
  ValidatedChallengeFile,
  ParsedChallengeFile
} from "@fat-dash/domain/csv-etl/types"
import { ChallengeCreateError } from "@fat-dash/domain/csv-etl/ChallengeCreateError"
import { parseChallengeFileNameOrThrow } from "@fat-dash/domain/csv-etl/parse"

function getValidatedChallengeFile(
  file: ParsedChallengeFile
): ValidatedChallengeFile {
  parseChallengeFileNameOrThrow(file.name)
  const csvNamedSections = createCsvNamedSections(file)
  const gameVersion = getGameVersionFromRawSummarySection(
    csvNamedSections["Summary"]
  )
  validateSectionsOrThrow(csvNamedSections, gameVersion, file.name)
  return {
    csvNamedSections,
    gameVersion
  }
}

function createCsvNamedSections(file: ParsedChallengeFile) {
  if (file.content.length !== 4) throw new Error("Expected 4 CSV sections.")
  return {
    Kill: file.content[0],
    Weapon: file.content[1],
    Summary: file.content[2],
    Technical: file.content[3]
  }
}

function validateSectionsOrThrow(
  csvSections: CsvNamedSections,
  gameVersion: GameVersion,
  fileName: string
) {
  try {
    validateKillSection(csvSections["Kill"], gameVersion)
    validateWeaponSection(csvSections["Weapon"], gameVersion)
    validateSummarySection(csvSections["Summary"], gameVersion)
    validateTechnicalSection(csvSections["Technical"], gameVersion)
  } catch (e) {
    throw new ChallengeCreateError(
      `${(e as Error).message || "content parse error"}`,
      fileName
    )
  }
}

const validateKillSection = (
  csvSection: CsvRawSectionContent,
  gameVersion: GameVersion
) => _validateHeaderEqual(csvSection[0], killHeaderNames(gameVersion))

const validateWeaponSection = (
  csvSection: CsvRawSectionContent,
  gameVersion: GameVersion
) => _validateHeaderEqual(csvSection[0], weaponHeaderNames(gameVersion))

const validateSummarySection = (
  csvSection: CsvRawSectionContent,
  gameVersion: GameVersion
) => validateKeyValueSection(csvSection, summaryHeaderNames(gameVersion))

const validateTechnicalSection = (
  csvSection: CsvRawSectionContent,
  gameVersion: GameVersion
) => validateKeyValueSection(csvSection, technicalHeaderNames(gameVersion))

const validateKeyValueSection = (
  csvSection: CsvRawSectionContent,
  sectionKeys: string[]
) =>
  csvSection.forEach((row, idx) => {
    if (row.length > 2)
      throw new Error(`csv validation: No valid key value section. \
            Expected one key and only one value column.`)

    _validateKeyEqual(row[0], sectionKeys[idx])
  })

const _validateHeaderEqual = (
  headerActual: string[],
  headerExpected: string[]
) => {
  if (
    !(
      headerActual.length === headerExpected.length &&
      // do not make difference between undefined values null and ""
      // implementation (up to this point papa parse) parses both as null
      headerActual.every((v, i) =>
        !headerExpected[i] ? !v : v === headerExpected[i]
      )
    )
  )
    throw new Error(
      `CSV format not correct. Expected Header\n${headerExpected}. \
actual is\n${headerActual}`
    )
}

const _validateKeyEqual = (keyActual: string, keyExpected: string) => {
  if (keyActual !== keyExpected)
    throw new Error(`CSV format not correct. Expected key "${keyExpected}". \
Actual is "${keyActual}".`)
}

export { getValidatedChallengeFile }
