import { PrimitiveLike } from "@fat-dash/ui/form-chart-widget-kit/types";

const mapCsvContentWithHeader = (section: any[][], propertyNames: string[]) => {
  const data: Array<{ [key: string]: PrimitiveLike }> = []
  section.forEach((dataRow, index) => {
    if (index !== 0) {
      data.push(dataRowToObj(dataRow, propertyNames))
    }
  })
  return data

  function dataRowToObj(csvDataRow: any[], _propertyNames: string[]) {
    const out: { [key: string]: PrimitiveLike } = {}
    return csvDataRow.reduce((accObj, cellValue, idx) => {
      accObj[_propertyNames[idx]] = cellValue
      return accObj
    }, out)
  }
}

const mapKeyValueCsvContent = (section: any[][], propertyNames: string[]) => {
  const record: { [key: string]: PrimitiveLike } = {}
  // convert key value section to single entity
  section.forEach((dataRow, index) => {
    record[propertyNames[index]] = dataRow[1]
  })
  return record
}

export { mapCsvContentWithHeader, mapKeyValueCsvContent }
