import { readBlobFiles, FileReadResult } from "@fat-dash/app/read-blob-files"

const readFiles: readBlobFiles = async function*(
  files: IterableIterator<File> | File[]
) {
  for (const file of files) yield readFile(file)
}

async function readFile(file: File): Promise<FileReadResult> {
  return new Promise<FileReadResult>(resolve =>
    /\.(csv)$/i.test(file.name)
      ? initFileReader(file, resolve)
      : resolve({
          fileName: file.name,
          errorMessage: `${file.name} has false extension. Expected .csv`,
          fileContent: null
        })
  )
}

/** Guaranteed to return a FileReadResult and not throwing an error */
function initFileReader(file: File, resolve: (value: FileReadResult) => void) {
  const reader = new FileReader()
  reader.onload = () =>
    typeof reader.result === "string"
      ? resolve({
          fileName: file.name,
          fileContent: reader.result,
          errorMessage: null
        })
      : resolve({
          fileName: file.name,
          errorMessage: `wrong file read type for file ${file.name}.
      Expected string, but did get ArrayBuffer or null.`,
          fileContent: null
        })

  reader.onerror = e =>
    resolve({
      fileName: file.name,
      errorMessage: `${file.name} read error occured.`,
      event: e,
      fileContent: null
    })

  reader.readAsText(file)
}

export { readFile, readFiles, initFileReader }
