import { CsvKillStatsRecord } from "../csv-record-types"
import {
  killHeaderNameProperty,
  weaponHeaderNameProperty,
  summaryHeaderNameProperty,
  technicalHeaderNameProperty,
  summaryHeaderNamePropertyV106
} from "../csv-file-template"
import { GameVersion, GameVersionCell } from "./version-types"

const gameVersionCells: GameVersionCell = {
  v106: [14, 1],
  lt_v106: [13, 1]
}

/**
 * @returns 1/-1, if aVer is higher/lower version than bVer. Else 0
 */
function compareGameVersions(aVer: string, bVer: string): 0 | -1 | 1 {
  const aParts = aVer.split(".")
  const bParts = bVer.split(".")

  // check for not equal version parts
  for (let i = 0; i < Math.min(aParts.length, bParts.length); i += 1) {
    const a = parseInt(aParts[i], 10)
    if (Number.isNaN(a) || a < 0)
      throw new Error(`could not parse version ${aParts[i]}`)
    const b = parseInt(bParts[i], 10)
    if (Number.isNaN(b) || b < 0)
      throw new Error(`could not parse version ${bParts[i]}`)

    if (a > b) return 1
    if (a < b) return -1
  }
  return aParts.length === bParts.length
    ? 0
    : aParts.length > bParts.length &&
      hasTrailingNonZeros(aParts.slice(bParts.length))
    ? 1
    : aParts.length < bParts.length &&
      hasTrailingNonZeros(bParts.slice(aParts.length))
    ? -1
    : 0

  function hasTrailingNonZeros(parts: string[]) {
    return parts.find(v => parseInt(v, 10) > 0)
  }
}

/**
 * In order to get the game version, iterate over each version change enum and
 * lookup if a correct value can be found.
 */
function getGameVersionFromRawSummarySection(
  summarySection: any[][]
): GameVersion {
  // sort desc from latest (left side/first) to lowest version (last)
  const gvOrdered = Object.entries(GameVersion)
    .sort((a, b) => compareGameVersions(b[1], a[1]))
    .map(e => e[0]) as Array<keyof typeof GameVersion>

  const gvKey = gvOrdered.find(key => {
    const cell = gameVersionCells[key]
    return (
      summarySection.length > cell[0] &&
      summarySection[cell[0]].length > cell[1] &&
      compareGameVersions(summarySection[cell[0]][cell[1]], GameVersion[key]) >=
        0
    )
  })
  if (!gvKey) throw new Error(`Could not find any game version indicator.`)
  return GameVersion[gvKey]
}

function getMergedChallengePropertyDescriptions() {
  return mergePropertyDescriptions([
    weaponHeaderNameProperty,
    summaryHeaderNameProperty,
    technicalHeaderNameProperty,
    summaryHeaderNamePropertyV106
  ])
}

function mergePropertyDescriptions<K extends string, V>(
  propDescr: Array<Array<[V, K]>>
) {
  return propDescr.reduce(
    (prev, cur) => ({
      ...prev,
      ...mapValueKeyTuplesToObject(cur)
    }),
    {} as { [key in K]: V }
  )
}

function mapValueKeyTuplesToObject<K extends string, V>(tuples: Array<[V, K]>) {
  return tuples.reduce(
    (prev, cur) => {
      prev[cur[1]] = cur[0]
      return prev
    },
    {} as { [key in K]: V }
  )
}

function getMergedKillPropertyDescriptions() {
  // tslint:disable-next-line:no-object-literal-type-assertion
  const out = {} as { [key in keyof CsvKillStatsRecord]: string }
  ;([killHeaderNameProperty] as Array<
    Array<[string, keyof CsvKillStatsRecord]>
  >).forEach(e =>
    e.reduce((prev, cur) => {
      prev[cur[1]] = cur[0]
      return prev
    }, out)
  )
  return out
}

export {
  getGameVersionFromRawSummarySection,
  compareGameVersions,
  getMergedKillPropertyDescriptions,
  getMergedChallengePropertyDescriptions
}
