import { parseChallengeFileNameOrThrow } from "./parse"
import { mapChallengeFromFile } from "./mapChallengeFromFile"
import { mapKillFromFile } from "./mapKillFromFile"
import {
  mapCsvTechnicalStatsRecord,
  mapCsvSummaryStatsRecord,
  mapCsvKillStatsRecords,
  mapCsvWeaponStatsRecord
} from "./csv-record-map-props"
import { getValidatedChallengeFile } from "@fat-dash/domain/csv-etl/validate-csv"
import {
  Challenge,
  ParsedChallengeFile,
  AnemicWriteChallenge
} from "@fat-dash/domain/csv-etl/types"
import { mapWeaponsFromFile } from "@fat-dash/domain/csv-etl/mapWeaponsFromFile"

/**
 * Factory function to create a Challenge in the domain model given a parsed
 * csv file as user input
 * @param file parsed file
 */
function createChallenge(file: ParsedChallengeFile): Challenge {
  const valFile = getValidatedChallengeFile(file)
  const fileName = parseChallengeFileNameOrThrow(file.name)
  const out: AnemicWriteChallenge = {
    ...mapChallengeFromFile(
      mapCsvSummaryStatsRecord(
        valFile.csvNamedSections["Summary"],
        valFile.gameVersion
      ),
      mapCsvTechnicalStatsRecord(
        valFile.csvNamedSections["Technical"],
        valFile.gameVersion
      ),
      fileName
    ),
    /* Hide the subentities in domain layer and only expose the aggregate root
    named Challenge. Repositories in infrastructure are the only place that 
    know of these internals. They cast the entity into an anemic form to be 
    able to save all its details */
    kills: mapKillFromFile(
      mapCsvKillStatsRecords(
        valFile.csvNamedSections["Kill"],
        valFile.gameVersion
      ),
      fileName
    ),
    weapons: mapWeaponsFromFile(
      mapCsvWeaponStatsRecord(
        valFile.csvNamedSections["Weapon"],
        valFile.gameVersion
      )
    )
  }
  return out
}

export { createChallenge }
