import { initFileReader } from "@fat-dash/infrastructure/fs/read-file"
import { createChallenge } from "@fat-dash/domain/csv-etl/createChallenge"
import { FileReadResult } from "@fat-dash/app/read-blob-files"
import { CreateChallengeResult } from "@fat-dash/app/types"
import { delay } from "@fat-dash/common/async"
import { parseCsvSections } from "@fat-dash/domain/csv-etl/parse"

type WorkerContext = {
  onmessage(this: Worker, ev: { data: File[] }): void
  postMessage(message: CreateChallengeResult): void
}

const workerCtx: WorkerContext = self as any

workerCtx.onmessage = async e => {
  let pendingCount = 0
  for (let idx = 0; idx < e.data.length; idx++) {
    while (true) {
      if (pendingCount <= PENDING_COUNT_MAX) {
        initFileReader(e.data[idx], onFileReadResult)
        pendingCount += 1
        break
      } else await delay(0)
    }
  }

  function onFileReadResult(readResult: FileReadResult) {
    pendingCount = Math.max(0, pendingCount - 1)
    processFileReadResult(readResult)
  }
}

function processFileReadResult(readResult: FileReadResult) {
  if (readResult.errorMessage === null) {
    try {
      workerCtx.postMessage({
        challenge: createChallenge({
          content: parseCsvSections(readResult.fileContent),
          name: readResult.fileName
        }),
        fileName: readResult.fileName
      })
    } catch (e) {
      workerCtx.postMessage({
        challenge: null,
        fileName: readResult.fileName,
        errorMessage: e.message
      })
    }
  } else {
    workerCtx.postMessage({
      challenge: null,
      fileName: readResult.fileName,
      errorMessage: readResult.errorMessage
    })
  }
}

const PENDING_COUNT_MAX = navigator.hardwareConcurrency
