import { CsvKillStatsRecord } from "./csv-record-types"
import {
  parseIntOrThrow,
  parseFloatOrThrow
} from "@fat-dash/domain/util/number"
import { KillFromFile, ChallengeFileName } from "./types"
import { Header } from "@fat-dash/domain/report/types"
import { stripColon } from "./util"
import { getMergedKillPropertyDescriptions } from "./version/version-api"

const mapKillFromFile = (
  csvKillSectionRecords: CsvKillStatsRecord[],
  fileName: ChallengeFileName
): KillFromFile[] => {
  return csvKillSectionRecords.map(r => {
    // time already given in csvKillStats column in format 'HH:mm:ss[:SSS]',
    // so concatenate this time with date from file info
    const parsedCsvTime = parseCsvEntityTime(r.time, fileName.full)

    // challengeDate is local date from csv file name, so do not use utc methods
    const killDate = new Date(fileName.date)
    killDate.setHours(parsedCsvTime[0])
    killDate.setMinutes(parsedCsvTime[1])
    killDate.setSeconds(parsedCsvTime[2])
    killDate.setMilliseconds(parsedCsvTime[3])

    const { time, ttk, ...rest } = r
    return {
      ...rest,
      ttk: parseFloatOrThrow(
        r.ttk.split("s")[0],
        `ttk ${ttk} from csvEntity, Challenge ${fileName.full}`
      ),
      killDate
    }
  })

  function parseCsvEntityTime(time: string, errorObjDescr: string) {
    const [hours, minutes, seconds, milis] = time.split(":")
    return [
      parseIntOrThrow(
        hours,
        `hours ${hours} from csvEntity, Challenge ${errorObjDescr}`
      ),
      parseIntOrThrow(
        minutes,
        `minutes ${minutes} from csvEntity, Challenge ${errorObjDescr}`
      ),
      parseIntOrThrow(
        seconds,
        `seconds ${seconds} from csvEntity, Challenge ${errorObjDescr}`
      ),
      parseIntOrThrow(
        milis,
        `miliseconds ${milis} from csvEntity, Challenge ${errorObjDescr}`
      )
    ]
  }
}

const getKillFromFilePropertyDescriptions = (): Header<
  KillFromFile
> => {
  const { time, ttk, ...rest } = stripColon(
    getMergedKillPropertyDescriptions()
  )
  return {
    ...rest,
    ttk: "TTK (in sec)",
    killDate: "Kill Date"
  }
}

export { mapKillFromFile, getKillFromFilePropertyDescriptions }
