import {
  CsvTechnicalStatsRecord,
  CsvSummaryStatsRecord,
  CsvKillStatsRecord,
  CsvWeaponStatsRecord
} from "./csv-record-types"
import {
  mapKeyValueCsvContent,
  mapCsvContentWithHeader
} from "./map-raw-csv-to-record"
import {
  technicalPropertyNames,
  summaryPropertyNames,
  killPropertyNames,
  weaponPropertyNames
} from "./version/version-api"
import { GameVersion } from "./version/version-types"

const mapCsvTechnicalStatsRecord = (
  technicalSection: any[][],
  gameVersion: GameVersion
) =>
  mapKeyValueCsvContent(
    technicalSection,
    technicalPropertyNames(gameVersion)
  ) as CsvTechnicalStatsRecord

const mapCsvSummaryStatsRecord = (
  summarySection: any[][],
  gameVersion: GameVersion
) =>
  mapKeyValueCsvContent(
    summarySection,
    summaryPropertyNames(gameVersion)
  ) as CsvSummaryStatsRecord

const mapCsvKillStatsRecords = (
  killSection: any[][],
  gameVersion: GameVersion
) =>
  mapCsvContentWithHeader(
    killSection,
    killPropertyNames(gameVersion)
  ) as CsvKillStatsRecord[]

const mapCsvWeaponStatsRecord = (
  weaponSection: any[][],
  gameVersion: GameVersion
) =>
  mapCsvContentWithHeader(
    weaponSection,
    weaponPropertyNames(gameVersion)
  ) as CsvWeaponStatsRecord[]

export {
  mapCsvWeaponStatsRecord,
  mapCsvKillStatsRecords,
  mapCsvSummaryStatsRecord,
  mapCsvTechnicalStatsRecord
}
