import { CsvSummaryStatsRecord, CsvTechnicalStatsRecord } from "./csv-record-types"
import {
  ChallengeDimension,
  ChallengeFromFile,
  ScenarioDimension,
  ChallengeFileName
} from "./types"
import { Header } from "@fat-dash/domain/report/types"
import { getMergedChallengePropertyDescriptions } from "./version/version-api"
import { stripColon } from "@fat-dash/domain/csv-etl/util"

const mapChallengeFromFile = (
  csvSummaryStats: CsvSummaryStatsRecord,
  csvTechnicalStats: CsvTechnicalStatsRecord,
  file: ChallengeFileName
): ChallengeFromFile => {
  const challDimen = extractChallengeDimensionFromFile(file)
  const { kills, avgTtk, ...restcsvSummaryStats } = csvSummaryStats

  return {
    ...restcsvSummaryStats,
    ...csvTechnicalStats,
    ...challDimen,
    ...{ killCount: kills, ttkAvg: avgTtk }
  }
}

const extractChallengeDimensionFromFile = (
  fileName: ChallengeFileName
): ChallengeDimension => ({
  challengeDate: fileName.date,
  fileName: fileName.full,
  scenario: fileName.scenario
})

const getChallengeFromFilePropertyDescriptions = (): Header<
  ChallengeFromFile
> => {
  const { kills, avgTtk, ...challengePropertyDescr } = stripColon(
    getMergedChallengePropertyDescriptions()
  )

  return {
    ...challengeDimensionHeaderName,
    ...scenarioDimensionHeaderName,
    ...challengePropertyDescr,
    ...{ killCount: kills, ttkAvg: avgTtk }
  }
}

const scenarioDimensionHeaderName: Header<ScenarioDimension> = {
  scenario: "Scenario"
}

const challengeDimensionHeaderName: Header<ChallengeDimension> = {
  fileName: "Challenge File",
  challengeDate: "Challenge Date",
  ...scenarioDimensionHeaderName
}

export { mapChallengeFromFile, getChallengeFromFilePropertyDescriptions }
