import {
  killHeaderNameProperty,
  weaponHeaderNameProperty,
  summaryHeaderNamePropertyV106,
  summaryHeaderNameProperty,
  technicalHeaderNameProperty
} from "../csv-file-template"
import { GameVersion } from "./version-types"
import { exhaustiveSwitchCheck } from "@fat-dash/common/lang"

function propertyNames(template: string[][]) {
  return template.map(t => t[1])
}

function headerNames(template: string[][]) {
  return template.map(t => t[0])
}

function killPropertyNames(_range: GameVersion) {
  return propertyNames(killHeaderNameProperty)
}

function killHeaderNames(_range: GameVersion) {
  return headerNames(killHeaderNameProperty)
}

function weaponPropertyNames(_range: GameVersion) {
  return propertyNames(weaponHeaderNameProperty)
}

function weaponHeaderNames(_range: GameVersion) {
  return headerNames(weaponHeaderNameProperty)
}

function summaryPropertyNames(range: GameVersion) {
  return summaryTuple(range, propertyNames)
}

function summaryHeaderNames(range: GameVersion) {
  return summaryTuple(range, headerNames)
}

function summaryTuple(
  range: GameVersion,
  fetcher: (template: string[][]) => string[]
) {
  switch (range) {
    case GameVersion.lt_v106:
      return fetcher(summaryHeaderNameProperty)
    case GameVersion.v106:
      return fetcher(summaryHeaderNamePropertyV106)
    default:
  }
  return exhaustiveSwitchCheck(range)
}

function technicalPropertyNames(_range: GameVersion) {
  return propertyNames(technicalHeaderNameProperty)
}

function technicalHeaderNames(_range: GameVersion) {
  return headerNames(technicalHeaderNameProperty)
}

export {
  getMergedChallengePropertyDescriptions,
  getMergedKillPropertyDescriptions,
  getGameVersionFromRawSummarySection
} from "./version-service"

export {
  killPropertyNames,
  killHeaderNames,
  weaponPropertyNames,
  weaponHeaderNames,
  summaryPropertyNames,
  summaryHeaderNames,
  technicalPropertyNames,
  technicalHeaderNames
}
