import { Weapon } from "@fat-dash/domain/csv-etl/types"
import { CsvWeaponStatsRecord } from "@fat-dash/domain/csv-etl/csv-record-types"

function mapWeaponsFromFile(
  csvWeaponSectionRecords: CsvWeaponStatsRecord[]
): Weapon[] {
  return csvWeaponSectionRecords.map<Weapon>(
    ({ weapon, shots, hits, damageDone, damagePossible }) => ({
      weapon,
      shots,
      hits,
      damageDone,
      damagePossible
    })
  )
}

export { mapWeaponsFromFile }
