/**
 * @see https://github.com/Microsoft/TypeScript/wiki/Breaking-Changes#extending-built-ins-like-error-array-and-map-may-no-longer-work
 */
class ChallengeCreateError extends Error {
  constructor(m: string, public fileName: string) {
    super(m)

    // Set the prototype explicitly.
    Object.setPrototypeOf(this, ChallengeCreateError.prototype)
  }
}

export { ChallengeCreateError }
