import { parseIntOrThrow } from "../util/number"
import {
  ChallengeFileName,
  CsvRawSections
} from "@fat-dash/domain/csv-etl/types"
import { parseCsv } from "@fat-dash/ccc"

function parseChallengeFileNameOrThrow(name: string): ChallengeFileName {
  const match = REGEX.exec(name)
  if (!match) {
    throw new Error(ERR_MSG)
  } else {
    const scenario = match[1]
    const year = parseIntOrThrow(match[2], `year in ${name}`)

    // convert given month to number between 0 and 11 (js spec)
    const month = parseIntOrThrow(match[3], `month in ${name}`) - 1

    const day = parseIntOrThrow(match[4], `day in ${name}`)
    const hours = parseIntOrThrow(match[5], `hours in ${name}`)
    const minutes = parseIntOrThrow(match[6], `minutes in ${name}`)
    const seconds = parseIntOrThrow(match[7], `seconds in ${name}`)

    // file name is local date, so use date without Date.UTC
    const date = new Date(year, month, day, hours, minutes, seconds)

    return { scenario, mode: "Challenge", full: name, date: date }
  }
}

// e.g. RexStrafesCata - Challenge - 2018.08.05-00.31.16 Stats.csv
const REGEX = /^(.*?)\s-\sChallenge\s-\s(\d{4})\.(\d{2})\.(\d{2})-(\d{2})\.(\d{2})\.(\d{2})\sStats.csv$/

const ERR_MSG = `Invalid file name, given ${name}. Did you try to import \
 something other than a challenge (free to play mode, ...)?`

function parseCsvSections(rawCsvContent: string): CsvRawSections {
  const out: CsvRawSections = []
  let currentSectionIndex = 0

  parseCsv(rawCsvContent).forEach(row => {
    if (row.length && row[0]) {
      out[currentSectionIndex] = out[currentSectionIndex] || []
      out[currentSectionIndex].push(row)
    } else currentSectionIndex += 1
  })
  return out
}

export { parseChallengeFileNameOrThrow, parseCsvSections }
